.nav {
  text-align: center;
  grid-area: nav;
  background-color: #61dafb;
}
.nav a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  margin-top: 1em;
}
.nav .active {
  color: #282c34;
}
.item {
  font-weight: bold;
  margin-top: 1em;
}

